.crt_wrapper {
    float: left;
    box-sizing: border-box;
    width: 100%;
    height: 80vh;
    margin: 0;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
}

.ctr_svg {
    border: 1px solid silver;
    width: 100%;
    flex-grow: 1;
}